"use client"

import { useEffect, useState } from "react"
import { MapWrapper } from "@/components/map-wrapper"
import { getDateCollections, getAllLocationsFromCollection, saveRouteAsActive } from "@/lib/firebase"
import type { LocationData } from "@/lib/types"
import { ChevronLeft, ChevronRight, Check } from "lucide-react"

export default function OperatorPage() {
  const [dateCollections, setDateCollections] = useState<string[]>([])
  const [currentIndex, setCurrentIndex] = useState(0)
  const [locations, setLocations] = useState<LocationData[]>([])
  const [loading, setLoading] = useState(true)
  const [saving, setSaving] = useState(false)
  const [message, setMessage] = useState("")

  // Load date collections on mount
  useEffect(() => {
    async function loadDateCollections() {
      console.log("[v0] Loading date collections...")
      const collections = await getDateCollections()
      console.log("[v0] Found collections:", collections)
      setDateCollections(collections)
      setLoading(false)
    }
    loadDateCollections()
  }, [])

  // Load locations when date changes
  useEffect(() => {
    if (dateCollections.length > 0) {
      loadLocationsForCurrentDate()
    }
  }, [currentIndex, dateCollections])

  async function loadLocationsForCurrentDate() {
    if (dateCollections.length === 0) return

    const collectionName = dateCollections[currentIndex]
    console.log("[v0] Loading locations for:", collectionName)

    setLoading(true)
    const locs = await getAllLocationsFromCollection(collectionName)
    console.log("[v0] Loaded locations:", locs.length)
    setLocations(locs)
    setLoading(false)
  }

  function handlePrevious() {
    if (currentIndex < dateCollections.length - 1) {
      setCurrentIndex(currentIndex + 1)
      setMessage("")
    }
  }

  function handleNext() {
    if (currentIndex > 0) {
      setCurrentIndex(currentIndex - 1)
      setMessage("")
    }
  }

  async function handleSetRoute() {
    if (dateCollections.length === 0 || locations.length === 0) return

    setSaving(true)
    setMessage("")

    const routeId = dateCollections[currentIndex]
    const success = await saveRouteAsActive(routeId, locations)

    if (success) {
      setMessage(`${routeId}のルートを設定しました`)
    } else {
      setMessage("ルートの設定に失敗しました")
    }

    setSaving(false)
  }

  const currentDate = dateCollections[currentIndex]
  const formattedDate = currentDate
    ? `${currentDate.slice(0, 4)}年${currentDate.slice(4, 6)}月${currentDate.slice(6, 8)}日`
    : ""

  // Convert locations to route coordinates
  const routeCoordinates: [number, number][] = locations.map((loc) => [loc.latitude, loc.longitude])

  if (loading && dateCollections.length === 0) {
    return (
      <div className="w-screen h-screen flex items-center justify-center bg-gray-100">
        <div className="text-gray-600">読み込み中...</div>
      </div>
    )
  }

  if (dateCollections.length === 0) {
    return (
      <div className="w-screen h-screen flex items-center justify-center bg-gray-100">
        <div className="text-gray-600">走行記録がありません</div>
      </div>
    )
  }

  return (
    <div style={{ width: "100vw", height: "100vh", position: "relative" }}>
      {/* Map */}
      <MapWrapper locations={[]} plannedRoute={routeCoordinates} />

      {/* Header */}
      <div
        style={{
          position: "fixed",
          top: 16,
          left: 16,
          right: 16,
          zIndex: 1000,
          display: "flex",
          justifyContent: "center",
        }}
      >
        <div
          style={{
            backgroundColor: "rgba(255, 255, 255, 0.95)",
            borderRadius: 16,
            padding: "16px 24px",
            boxShadow: "0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06)",
            display: "flex",
            alignItems: "center",
            gap: 16,
          }}
        >
          <button
            onClick={handlePrevious}
            disabled={currentIndex >= dateCollections.length - 1}
            style={{
              width: 48,
              height: 48,
              borderRadius: 9999,
              backgroundColor: currentIndex >= dateCollections.length - 1 ? "#e5e7eb" : "#3b82f6",
              color: "white",
              border: "none",
              cursor: currentIndex >= dateCollections.length - 1 ? "not-allowed" : "pointer",
              display: "flex",
              alignItems: "center",
              justifyContent: "center",
              transition: "all 0.2s",
            }}
          >
            <ChevronLeft size={24} />
          </button>

          <div style={{ textAlign: "center", minWidth: 200 }}>
            <div style={{ fontSize: 18, fontWeight: 700, color: "#1f2937" }}>{formattedDate}</div>
            <div style={{ fontSize: 14, color: "#6b7280", marginTop: 4 }}>{locations.length}ポイント</div>
          </div>

          <button
            onClick={handleNext}
            disabled={currentIndex === 0}
            style={{
              width: 48,
              height: 48,
              borderRadius: 9999,
              backgroundColor: currentIndex === 0 ? "#e5e7eb" : "#3b82f6",
              color: "white",
              border: "none",
              cursor: currentIndex === 0 ? "not-allowed" : "pointer",
              display: "flex",
              alignItems: "center",
              justifyContent: "center",
              transition: "all 0.2s",
            }}
          >
            <ChevronRight size={24} />
          </button>
        </div>
      </div>

      {/* Set Route Button */}
      <div
        style={{
          position: "fixed",
          bottom: 16,
          left: "50%",
          transform: "translateX(-50%)",
          zIndex: 1000,
        }}
      >
        <button
          onClick={handleSetRoute}
          disabled={saving || locations.length === 0}
          style={{
            backgroundColor: saving || locations.length === 0 ? "#9ca3af" : "#10b981",
            color: "white",
            padding: "16px 32px",
            borderRadius: 16,
            border: "none",
            fontSize: 16,
            fontWeight: 700,
            cursor: saving || locations.length === 0 ? "not-allowed" : "pointer",
            boxShadow: "0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06)",
            display: "flex",
            alignItems: "center",
            gap: 8,
            transition: "all 0.2s",
          }}
        >
          <Check size={20} />
          {saving ? "設定中..." : "このルートを設定"}
        </button>
      </div>

      {/* Message */}
      {message && (
        <div
          style={{
            position: "fixed",
            top: 100,
            left: "50%",
            transform: "translateX(-50%)",
            zIndex: 1001,
            backgroundColor: "rgba(16, 185, 129, 0.95)",
            color: "white",
            padding: "12px 24px",
            borderRadius: 12,
            fontSize: 14,
            fontWeight: 600,
            boxShadow: "0 4px 6px -1px rgba(0, 0, 0, 0.1)",
          }}
        >
          {message}
        </div>
      )}
    </div>
  )
}
